import React from "react";
import Sidebar from "grommet/components/Sidebar";
import Heading from "grommet/components/Headline";
import Link from "gatsby-link";
import Animate from "grommet/components/Animate";
import PropTypes from 'prop-types';

import MainMenu from "./MainMenu.jsx";
import BottomFooter from "./BottomFooter.jsx";

export default class Aside extends React.PureComponent {

  constructor() {
    super();
    this.state = {
      headerColor: undefined,
      headerAbout: false,
      activeMenuClass: {
        video: "",
        web: "",
        android: ""
      }
    }
    this.mouseEntered = this.mouseEntered.bind(this);
    this.mouseLeft = this.mouseLeft.bind(this);
    this.getAbout = this.getAbout.bind(this);
    this.getActiveMenuClass = this.getActiveMenuClass.bind(this);
  }

  componentDidMount() {
    let color = this.props.colorOnMount;
    let activeMenuClass = this.getActiveMenuClass(color);
    this.setState({
      headerColor: color,
      activeMenuClass: activeMenuClass
    });
  }

  handleChangedColor(color) {
    let activeMenuClass = this.getActiveMenuClass(color);
    this.setState({
      headerColor: color,
      activeMenuClass: activeMenuClass
    });
  }

  mouseEntered() {
    this.setState({
      headerAbout: true
    });
  }
  mouseLeft() {
    this.setState({
      headerAbout: false
    });
  }

  getAbout() {
    if (this.state.headerAbout) {
      return (
        <Animate className="about"
          enter={{ animation: "slide-right", duration: 300, delay: 0 }}
          keep={true}
        >
          <span>about</span>
        </Animate>
      )
    } else {
      return "";
    }
  }

  getActiveMenuClass(color) {
    let activeMenuClass = {};
    switch (color) {
      case this.props.colorsBG.video:
        activeMenuClass = {
          video: "menu-active",
          web: "",
          android: ""
        }
        break;
      case this.props.colorsBG.web:
        activeMenuClass = {
          video: "",
          web: "menu-active",
          android: ""
        }
        break;
      case this.props.colorsBG.android:
        activeMenuClass = {
          video: "",
          web: "",
          android: "menu-active"
        }
        break;

      default:
        activeMenuClass = {
          video: "",
          web: "",
          android: ""
        }
        break;
    }
    return activeMenuClass;
  }


  render() {
    const about = this.getAbout();

    return <Sidebar fixed={false} full={false} className="sidebar">
      <Link onMouseEnter={this.mouseEntered}
        onMouseLeave={this.mouseLeft} onClick={this.handleChangedColor.bind(this, this.props.colorsBG.home)} className="header-class" style={{ backgroundColor: this.state.headerColor }} to="/">
        {about}
        <Heading className="heading-class">TS</Heading>
      </Link>
      <MainMenu activeMenuClass={this.state.activeMenuClass} colors={this.props.colorsBG} menuClicked={this.handleChangedColor.bind(this)} />
      <BottomFooter />
    </Sidebar>;
  }
}

Aside.propTypes = {
  colorsBG: PropTypes.object,
  colorOnMount: PropTypes.string
};


