import React from "react"
import Link from "gatsby-link"
import Menu from "grommet/components/Menu"
import Box from "grommet/components/Box"
import PropTypes from 'prop-types'

export default class MainMenu extends React.PureComponent {

  constructor() {
    super();
    this.handleMenuClick = this.handleMenuClick.bind(this);
  }

  handleMenuClick(color){
    this.props.menuClicked(color);
  }

  render() {
    let active = this.props.activeMenuClass;
    return (
      <Box flex="grow" justify="start">
        <Menu className="main-menu" primary={true}>
          <Link onClick={this.handleMenuClick.bind(this, this.props.colors.video)} className={"menu-link-video " + active.video} to="/video-production">
            Video Production
            </Link>
          <Link onClick={this.handleMenuClick.bind(this, this.props.colors.web)} className={"menu-link-web " + active.web} to="/web-development">
            Web Development
            </Link>
          <Link onClick={this.handleMenuClick.bind(this, this.props.colors.android)} className={"menu-link-android " + active.android} to="/android">
            Android
            </Link>
        </Menu>
      </Box>
    )
  }
}

MainMenu.propTypes = {
  colors: PropTypes.object,
  activeMenuClass: PropTypes.object
};


